h2 {
  margin: 0 auto 2rem auto;
}

body {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: auto auto auto auto;
  grid-gap: 1rem;

  font-family: "Rubik", sans-serif;
  font-size: 18px;
  background-color: #1b69f9;
  margin: 2rem;
}

.header__header {
  color: white;
  grid-column: span 3;
  display: flex;
  align-items: center;
  margin: 2rem auto 3rem 0;
}

.header__header > h1 {
  margin-right: 2rem;
  font-size: 3em;
}

.header__city-name {
  font-style: oblique;
  font-size: 2rem;
}

.header__city-name::before,
.header__city-name::after {
  content: "âœ¨";
}

.temperature__section,
.sky__section,
.city-name__section {
  border-radius: 8px;
  padding: 2rem;
  background-color: white;
}

.temperature__section {
  grid-row: 2;
}

#currentTempButton {
  background-color: #1b69f9;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 10px;
  box-shadow: 2px 2px 2px gray;
}

#currentTempButton:active {
  background-color: #0f3a8a;
  box-shadow: none;
}

.sky__section {
  grid-row: 3;
}

#skySelect {
  width: 200px;
  height: 40px;
  border-radius: 5px;
  border: 2px solid black;
}

.city-name__section {
  grid-row: 4;
}

#cityNameInput {
  height: 40px;
  border-radius: 5px;
  border: 2px solid black;
}

.garden__section {
  grid-row: 2 / span 3;
  grid-column: 2;
  text-align: center;
  align-self: center;
}

.temperature__content {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  /* justify-content: center; */
}

.temp-type__buttons {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.temp-type__buttons button {
  height: 50px;
  border: none;
  color: white;
  background-color: #1b69f9;
  border-radius: 5px;
  box-shadow: 2px 2px 2px gray;
}

.temp-type__buttons button:active {
  background-color: #0f3a8a;
  box-shadow: none;
}

#tempValue {
  font-size: 3rem;
  margin-left: 1.5rem;
  margin-right: 1.5rem;
  width: 50px;
}

.temperature__controls {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#increaseTempControl {
  cursor: pointer;
}

#increaseTempControl:hover {
  opacity: 50%;
}

#decreaseTempControl {
  cursor: pointer;
}

#decreaseTempControl:hover {
  opacity: 50%;
}

.garden__section > h2 {
  color: white;
}

.garden__content {
  min-height: 200px;
  max-width: fit-content;
  margin: auto;
  padding: 2rem;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  border-radius: 8px;
  font-size: 2em;
}

.city-name__reset-btn {
  border: 0;
  background-color: #1655cc;
  color: white;
  border-radius: 8px;
  padding: 1rem;
  font-family: "Rubik", sans-serif;
  box-shadow: 2px 2px 2px gray;
}

.city-name__reset-btn:active {
  background-color: #08204c;
  box-shadow: none;
}

.red {
  color: red;
}

.orange {
  color: orange;
}

.yellow {
  color: gold;
}

.yellow-green {
  color: yellowgreen;
}

.green {
  color: green;
}

.teal {
  color: teal;
}

.cloudy {
  background: linear-gradient(to bottom, lightgrey, lightgrey, rgb(3, 141, 54));
}

.sunny {
  background: linear-gradient(to bottom, rgb(50, 174, 236), rgb(50, 174, 236), rgb(3, 141, 54));
}

.rainy {
  background: linear-gradient(to bottom, lightblue, lightblue, rgb(1, 92, 35));
}

.snowy {
  background: linear-gradient(to bottom, lightsteelblue, lightsteelblue, rgb(216, 231, 221));
}
